import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import NyanCat from "@/views/NyanCat.vue";
import Photos from "@/views/Photos.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "home",
    component: Home
  },
  {
    path: "/nyanCat",
    name: "nyanCat",
    component: NyanCat
  },
  {
    path: "/photos",
    name: "photos",
    component: Photos
  }
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes
});

export default router;
